import { Component, OnInit } from '@angular/core';
import { CurrencyDataService } from 'src/app/components/expenses/services/currency-data.service';
import { CurrencyService } from '../../services/currency.service';

@Component({
	selector: 'app-currency',
	templateUrl: './currency.component.html',
	styleUrls: ['./currency.component.scss']
})
export class CurrencyComponent implements OnInit {

	public isCurrencyLoading: boolean = true;

	constructor(public currencyService: CurrencyService,
		private _currencyDataService: CurrencyDataService) { }

	public ngOnInit(): void {
		this._currencyDataService.currencyRates$
		.subscribe(() => {
			this.isCurrencyLoading = false;
		});
	}

}
