import { Component, OnInit, OnDestroy } from '@angular/core';
import { DateService } from 'src/app/services/date.service';
import { switchMap } from 'rxjs/operators';
import { MonthExpensesByCategory } from 'src/app/components/expenses/models/month-expenses-by-category.model';
import { MonthExpensesByCategoryService } from 'src/app/components/expenses/services/month-expenses-by-category.service';
import { Label } from 'ng2-charts';
import { ChartType, ChartOptions } from 'chart.js';
import { Subscription } from 'rxjs';

@Component({
	selector: 'app-pie',
	templateUrl: './pie.component.html',
	styleUrls: ['./pie.component.scss']
})
export class PieComponent implements OnInit, OnDestroy {

	private sub: Subscription = null;

	public colorComponentMax: number = 255;

	public pieChartData: number[] = [];

	public pieChartLabels: Label[] = [];

	public pieChartType: ChartType = 'pie';

	public pieChartColors: any[] = [
		{
		  backgroundColor: [],
		},
	];

	public pieChartOptions: ChartOptions = {
		legend: {
			position: 'left'
		}
	};

	public monthExpensesByCategoryList: MonthExpensesByCategory[] = [];

	constructor(public dateService: DateService,
		private _monthExpensesByCategoryService: MonthExpensesByCategoryService) { }

	public ngOnInit(): void {
		this.sub = this.dateService.date$
			.pipe(
				switchMap((date: moment.Moment) =>
					this._monthExpensesByCategoryService.loadMonthExpensesByCategory(date))
			).subscribe((monthExpensesByCategoryList: MonthExpensesByCategory[]) => {
				this.pieChartLabels.length = 0;
				this.pieChartData.length = 0;
				this.pieChartColors[0].backgroundColor.length = 0;

				monthExpensesByCategoryList.forEach((savedExpenses: MonthExpensesByCategory) => {
					this.pieChartLabels.push(savedExpenses.category);
					this.pieChartData.push(savedExpenses.amount);
					this.pieChartColors[0].backgroundColor.push(this.generateColor());
				});
				this.monthExpensesByCategoryList = monthExpensesByCategoryList;
			});
	}

	public generateColorComponent(): number {
		return Math.floor(Math.random() * this.colorComponentMax);
	}

	public generateColor(): string {
		return `rgba(`
			+ `${this.generateColorComponent()}` + `,`
			+ `${this.generateColorComponent()}` + `,`
			+ `${this.generateColorComponent()}` + `,`
			+ `.3)`;
	}

	public ngOnDestroy(): void {
		this.sub.unsubscribe();
	}

}
