import { Injectable } from '@angular/core';
import { CurrencyDataService } from './currency-data.service';
import { Currency } from '../models/currency.model';

@Injectable({
	providedIn: 'root'
})
export class CurrencyService {

	public usd: Currency = null;

	public euro: Currency = null;

	public rub: Currency = null;

	constructor(private _currencyDataService: CurrencyDataService) {
		this._currencyDataService.currencyRates$
		.subscribe((currencyRates: Currency[]) => {
			this.initCurrency(currencyRates);
		});
	}

	public initCurrency(currencyRates: Currency[]): void {
		currencyRates.forEach((currencyRate: Currency) => {
			switch (currencyRate.abbreviation) {
				case 'USD':
					this.usd = currencyRate;
					break;
				case 'EUR':
					this.euro = currencyRate;
					break;
				case 'RUB':
					this.rub = currencyRate;
					break;
			}
		});
	}
}
