import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { MonthExpensesTotal } from '../models/month-expenses-total.model';
import { map } from 'rxjs/operators';
import { GetResponse } from '../models/response.model';

@Injectable({
	providedIn: 'root'
})
export class MonthExpensesTotalService {

	private static url: string = 'https://money-manager-project.firebaseio.com/monthExpensesTotal';

	constructor(private _http: HttpClient) { }

	public loadMonthExpensesTotal(date: moment.Moment): Observable<MonthExpensesTotal[]> {
		return this._http
			.get<MonthExpensesTotal[]>(`${MonthExpensesTotalService.url}/${date.format('MMMM-YYYY')}.json`)
			.pipe(
				map((monthExpensesTotal: any) => {
					if (!monthExpensesTotal) {
						return [];
					}
					return Object.keys(monthExpensesTotal)
						.map((key: string) => ({ ...monthExpensesTotal[key], id: key }));
				})
			);
	}

	public saveMonthExpensesTotal(monthExpensesTotal: MonthExpensesTotal): Observable<MonthExpensesTotal> {
		return this._http
			.post<GetResponse>(`${MonthExpensesTotalService.url}/${monthExpensesTotal.month}.json`, monthExpensesTotal)
			.pipe(map((response: any) => {
				return { ...monthExpensesTotal, id: response.name };
			}));
	}

	public removeMonthExpensesTotal(monthExpensesTotal: MonthExpensesTotal): Observable<void> {
		return this._http
			.delete<void>(`${MonthExpensesTotalService.url}/${monthExpensesTotal.month}/.json`);
	}
}
