import { Injectable } from '@angular/core';
import { Category } from '../models/category.model';
import { CategoriesDataService } from './categories-data.service';

@Injectable({
	providedIn: 'root'
})
export class CategoriesService {

	public savedCategories: Category[] = [];

	public newCategoryInput: string = '';

	public placeholder: string = 'Add new category here';

	public timeout: number = 1500;

	constructor(private _categoriesDataService: CategoriesDataService) {
		this._categoriesDataService.categories$
		.subscribe((categories: Category[]) => {
			this.initCategories(categories);
		});
	}

	private initCategories(categories: Category[]): void {
		this.savedCategories = categories;
	}

	public addNewCategory(): void {
		const categoriesArray: string[] = [];
		this.savedCategories.forEach((category: Category) => {
			categoriesArray.push(category.name);
		});
		if (categoriesArray.indexOf(this.newCategoryInput) === -1) {
			this.savedCategories.push(
				new Category(this.newCategoryInput)
			);
		} else {
			this.placeholder = 'This category already exists';
			setTimeout(() => {
				this.placeholder = 'Add new category here';
			}, this.timeout);
		}
		this._categoriesDataService.saveCategoriesToStorage(this.savedCategories);
		this.newCategoryInput = '';
	}
}
