import { Component, OnInit, Output } from '@angular/core';
import { CategoriesService } from 'src/app/components/expenses/services/categories.service';
import { Category } from 'src/app/components/expenses/models/category.model';
import { EventEmitter } from '@angular/core';

@Component({
	selector: 'app-category-list',
	templateUrl: './category-list.component.html',
	styleUrls: ['./category-list.component.scss']
})

export class CategoryListComponent {

	public isCategorySelected: boolean = false;

	public isCategoryListOpen: boolean = false;

	@Output()
	public selectedCategory: EventEmitter<Category> = new EventEmitter<Category>();

	constructor(public categoriesService: CategoriesService) { }

	public selectCategory(category: Category): void {
		this.isCategorySelected = true;
		this.selectedCategory.emit(category);
	}

	public openCategoryList(): void {
		this.isCategoryListOpen = !this.isCategoryListOpen;
	}
}
