export class Currency {

	constructor(
		public abbreviation: string,
		public id: number,
		public name: string,
		public officialRate: number,
		public scale: number,
		public date: string
	) {}

	public static fromJSON(item: any): Currency {
		return Boolean(item)
			? new Currency(
				item.Cur_Abbreviation,
				item.Cur_ID,
				item.Cur_Name,
				item.Cur_OfficialRate,
				item.Cur_Scale,
				item.Date)
			: null;
	}
}
