export class Category {

	constructor(
		public name: string
	) {}

	public static fromJSON(item: any): Category {
		return Boolean(item)
			? new Category(item.name)
			: null;
	}

	public static toJSON(category: Category): any {
		return Boolean(category)
			? {
				name: category.name
			}
			: {};
	}
}

export const initialCategory: Category[] = [
	new Category('Products'),
	new Category('Housekeeping'),
	new Category('Auto')
];
