import { Component, OnInit } from '@angular/core';
import { Category } from 'src/app/components/expenses/models/category.model';
import { DateService } from 'src/app/services/date.service';
import * as moment from 'moment';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ExpensesService } from 'src/app/components/expenses/services/expenses.service';
import { ExpensesList } from 'src/app/components/expenses/models/expenses-list.model';
import { Expenses } from 'src/app/components/expenses/models/expenses.model';
import { switchMap } from 'rxjs/operators';
import { MonthExpensesByCategory } from 'src/app/components/expenses/models/month-expenses-by-category.model';
import { combineLatest } from 'rxjs';
import { MonthExpensesByCategoryService } from 'src/app/components/expenses/services/month-expenses-by-category.service';
import { MonthExpensesTotal } from 'src/app/components/expenses/models/month-expenses-total.model';
import { MonthExpensesTotalService } from 'src/app/components/expenses/services/month-expenses-total.service';

@Component({
	selector: 'app-expenses',
	templateUrl: './expenses.component.html',
	styleUrls: ['./expenses.component.scss']
})
export class ExpensesComponent implements OnInit {

	public isLoading: boolean = true;

	public selectedCategory: Category = null;

	public minNumberForInputValidation: number = 0.01;

	public form: FormGroup = new FormGroup({
		expensesAmountInput: new FormControl('', [Validators.min(this.minNumberForInputValidation), Validators.required])
	});

	public expensesList: ExpensesList[] = [];

	public monthExpensesByCategoryList: MonthExpensesByCategory[] = [];

	public monthExpensesTotalList: MonthExpensesTotal[] = [];

	public monthExpensesTotalAmount: number = 0;

	public selectedCategoryExpensesAmount: number = null;

	public selectedCategoryExpenses: Expenses = null;

	constructor(public dateService: DateService,
		private _expensesService: ExpensesService,
		private _monthExpensesByCategoryService: MonthExpensesByCategoryService,
		private _monthExpensesTotalService: MonthExpensesTotalService) { }

	public ngOnInit(): void {
		this.dateService.date$
			.pipe(
				switchMap((date: moment.Moment) =>
					combineLatest([
						this._expensesService.loadExpenses(date),
						this._monthExpensesByCategoryService.loadMonthExpensesByCategory(date),
						this._monthExpensesTotalService.loadMonthExpensesTotal(date)
					])
		)).subscribe(([expensesList, monthExpensesByCategoryList, monthExpensesTotalList]
			: [ExpensesList[], MonthExpensesByCategory[], MonthExpensesTotal[]]) => {
			this.expensesList = expensesList;
			this.monthExpensesByCategoryList = monthExpensesByCategoryList;
			this.monthExpensesTotalList = monthExpensesTotalList;
			this.monthExpensesTotalAmount = monthExpensesTotalList.length > 0 ? monthExpensesTotalList[0].amount : 0;
			this.isLoading = false;
		});
	}

	public onSubmit(): void {

		// Saving daily expenses
		this.selectedCategoryExpensesAmount = this.form.value.expensesAmountInput;
		this.selectedCategoryExpenses = {
			category: this.selectedCategory,
			amount: this.selectedCategoryExpensesAmount
		};
		const expenses: ExpensesList = {
			day: this.dateService.date$.value.format('DD-MM-YYYY'),
			expenses: this.selectedCategoryExpenses
		};

		const savedCategoriesArray: string[] = [];
		this.expensesList.forEach((savedExpenses: ExpensesList) => {
			savedCategoriesArray.push(savedExpenses.expenses.category.name);
		});
		if (savedCategoriesArray.indexOf(expenses.expenses.category.name) === -1) {
			this.create(expenses);
		} else {
			this.expensesList.forEach((savedExpenses: ExpensesList) => {
				if (savedExpenses.expenses.category.name === this.selectedCategory.name) {
					expenses.expenses.amount += savedExpenses.expenses.amount;
					this.remove(savedExpenses);
					this.create(expenses);
				}
			});
		}

		// Saving month expenses by category
		const monthExpensesByCategory: MonthExpensesByCategory = {
			month: this.dateService.date$.value.format('MMMM-YYYY'),
 			category: this.selectedCategory.name,
			amount: this.selectedCategoryExpensesAmount
		};

		const savedMonthCategoriesArray: string[] = [];
		this.monthExpensesByCategoryList.forEach((savedMonthExpensesByCategory: MonthExpensesByCategory) => {
			savedMonthCategoriesArray.push(savedMonthExpensesByCategory.category);
		});

		if (savedMonthCategoriesArray.indexOf(monthExpensesByCategory.category) === -1) {
			this.createMonthExpensesByCategory(monthExpensesByCategory);
		} else {
			this.monthExpensesByCategoryList.forEach((savedMonthExpensesByCategory: MonthExpensesByCategory) => {
				if (savedMonthExpensesByCategory.category === this.selectedCategory.name) {
					monthExpensesByCategory.amount += savedMonthExpensesByCategory.amount;
					this.removeMonthExpensesByCategory(savedMonthExpensesByCategory);
					this.createMonthExpensesByCategory(monthExpensesByCategory);
				}
			});
		}

		// Saving total month expenses
		const monthExpensesTotal: MonthExpensesTotal = {
			month: this.dateService.date$.value.format('MMMM-YYYY'),
			amount: this.selectedCategoryExpensesAmount
		};

		const savedMonthExpensesTotalArray: string[] = [];
		this.monthExpensesTotalList.forEach((savedMonthExpensesTotal: MonthExpensesTotal) => {
			savedMonthExpensesTotalArray.push(savedMonthExpensesTotal.month);
		});

		if (savedMonthExpensesTotalArray.indexOf(monthExpensesTotal.month) === -1) {
			this.createMonthExpensesTotal(monthExpensesTotal);
		} else {
			this.monthExpensesTotalList.forEach((savedMonthExpensesTotal: MonthExpensesTotal) => {
				if (savedMonthExpensesTotal.month === monthExpensesTotal.month) {
					monthExpensesTotal.amount += savedMonthExpensesTotal.amount;
					this.removeMonthExpensesTotal(savedMonthExpensesTotal);
					this.createMonthExpensesTotal(monthExpensesTotal);
				}
			});
		}

		this.selectedCategoryExpenses = null;
		this.selectedCategory = null;
		this.selectedCategoryExpensesAmount = null;
	}

	public create(expenses: ExpensesList): void {
		this._expensesService.saveExpenses(expenses).subscribe((expense: ExpensesList) => {
			this.expensesList.push(expense);
			this.form.reset();
		}, (err: Error) => console.error(err));
	}

	public remove(expenses: ExpensesList): void {
		const monthExpensesTotal: MonthExpensesTotal = {
			month: this.dateService.date$.value.format('MMMM-YYYY'),
			amount: this.monthExpensesTotalAmount
		};
		this.removeMonthExpensesTotal(monthExpensesTotal);
		monthExpensesTotal.amount -= expenses.expenses.amount;
		this.createMonthExpensesTotal(monthExpensesTotal);

		this._expensesService.removeExpenses(expenses).subscribe(() => {
			this.expensesList = this.expensesList.filter((value: ExpensesList) => value.id !== expenses.id);
		}, (err: Error) => console.error(err));
	}

	public createMonthExpensesByCategory(monthExpensesByCategory: MonthExpensesByCategory): void {
		this._monthExpensesByCategoryService.saveMonthExpensesByCategory(monthExpensesByCategory)
		.subscribe((monthExpensesByCategoryData: MonthExpensesByCategory) => {
			this.monthExpensesByCategoryList.push(monthExpensesByCategoryData);
		}, (err: Error) => console.error(err));
	}

	public removeMonthExpensesByCategory(monthExpensesByCategory: MonthExpensesByCategory): void {
		this._monthExpensesByCategoryService.removeMonthExpensesByCategory(monthExpensesByCategory)
		.subscribe(() => {
			this.monthExpensesByCategoryList = this.monthExpensesByCategoryList
				.filter(
					(value: MonthExpensesByCategory) => value.id !== monthExpensesByCategory.id);
		}, (err: Error) => console.error(err));
	}

	public createMonthExpensesTotal(monthExpensesTotal: MonthExpensesTotal): void {
		this._monthExpensesTotalService.saveMonthExpensesTotal(monthExpensesTotal)
		.subscribe((monthExpensesTotalData: MonthExpensesTotal) => {
			this.monthExpensesTotalList.push(monthExpensesTotalData);
			this.monthExpensesTotalAmount = monthExpensesTotalData.amount;
		}, (err: Error) => console.error(err));
	}

	public removeMonthExpensesTotal(monthExpensesTotal: MonthExpensesTotal): void {
		this._monthExpensesTotalService.removeMonthExpensesTotal(monthExpensesTotal)
		.subscribe(() => {
			this.monthExpensesTotalList = this.monthExpensesTotalList
				.filter(
					(value: MonthExpensesTotal) => value.id !== monthExpensesTotal.id);
		}, (err: Error) => console.error(err));
	}

	public saveSelectedCategory(category: Category): void {
		this.selectedCategory = category;
	}

	public resetSelectedCategory(): void {
		this.selectedCategory = null;
	}
}
