import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Currency } from '../models/currency.model';

@Injectable({
	providedIn: 'root'
})
export class CurrencyDataService {

	private static url: string = 'https://www.nbrb.by/api/exrates/rates?periodicity=0';

	public currencyRates$: Observable<Currency[]> = this.loadCurrency();

	constructor(private _http: HttpClient) { }

	public loadCurrency(): Observable<Currency[]> {
		return this._http
			.get<Currency[]>(`${CurrencyDataService.url}`)
			.pipe(
				map((json: any) => {
					return (json || [])
					.filter(Boolean)
					.map(Currency.fromJSON);
				})
			);
	}
}
