import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Category } from '../models/category.model';
import { map } from 'rxjs/operators';
import { of, Observable } from 'rxjs';

@Injectable({
	providedIn: 'root'
})
export class CategoriesDataService {

	private static categoriesLocalStorageKey: string = 'savedCategories';

	private static baseUrl: string = 'assets';

	public categories$: Observable<Category[]> = this.loadSavedCategories();

	constructor(private _httpClient: HttpClient) { }

	private loadSavedCategories(): Observable<Category[]> {
		const localStorageData: any = localStorage.getItem(CategoriesDataService.categoriesLocalStorageKey);
		if (localStorageData) {
			return of(
				(JSON.parse(localStorageData) || [])
					.filter(Boolean)
					.map(Category.fromJSON)
			);
		} else {
			return this._httpClient.get<Category[]>(`${CategoriesDataService.baseUrl}/defaultCategories.json`)
			.pipe(
				map((json: any) => {
					return (json || [])
					.filter(Boolean)
					.map(Category.fromJSON);
				})
			);
		}
	}

	public saveCategoriesToStorage(categories: Category[]): void {
		localStorage.setItem(
			CategoriesDataService.categoriesLocalStorageKey,
			JSON.stringify(
				categories.map(Category.toJSON)
			)
		);
	}
}
