import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { MonthExpensesByCategory } from '../models/month-expenses-by-category.model';
import { Observable, BehaviorSubject } from 'rxjs';
import { GetResponse } from '../models/response.model';

@Injectable({
	providedIn: 'root'
})
export class MonthExpensesByCategoryService {

	private static url: string = 'https://money-manager-project.firebaseio.com/monthExpensesByCategory';

	constructor(private _http: HttpClient) { }

	public loadMonthExpensesByCategory(date: moment.Moment): Observable<MonthExpensesByCategory[]> {
		return this._http
			.get<MonthExpensesByCategory[]>(`${MonthExpensesByCategoryService.url}/${date.format('MMMM-YYYY')}.json`)
			.pipe(
				map((monthExpensesByCategory: any) => {
					if (!monthExpensesByCategory) {
						return [];
					}
					return Object.keys(monthExpensesByCategory)
						.map((key: string) => ({ ...monthExpensesByCategory[key], id: key }));
				})
			);
	}

	public saveMonthExpensesByCategory(monthExpensesByCategory: MonthExpensesByCategory): Observable<MonthExpensesByCategory> {
		return this._http
			.post<GetResponse>(`${MonthExpensesByCategoryService.url}/${monthExpensesByCategory.month}.json`, monthExpensesByCategory)
			.pipe(map((response: any) => {
				return { ...monthExpensesByCategory, id: response.name };
			}));
	}

	public removeMonthExpensesByCategory(monthExpensesByCategory: MonthExpensesByCategory): Observable<void> {
		return this._http
			.delete<void>(`${MonthExpensesByCategoryService.url}/${monthExpensesByCategory.month}/${monthExpensesByCategory.id}.json`);
	}
}
