import { Component, OnInit } from '@angular/core';
import { DateService } from 'src/app/services/date.service';
import * as moment from 'moment';
import { Day } from 'src/app/components/expenses/models/day.model';

@Component({
	selector: 'app-days-list',
	templateUrl: './days-list.component.html',
	styleUrls: ['./days-list.component.scss']
})
export class DaysListComponent implements OnInit {

	public dayList: Day[] = null;

	constructor(public dateService: DateService) { }

	public ngOnInit(): void {
		this.dateService.date$
		.subscribe(this.generateExpensesList.bind(this));
	}

	public generateExpensesList(currentDate: moment.Moment): void {
		const startDay: moment.Moment = currentDate.clone().startOf('month');
		const endDay: moment.Moment = currentDate.clone().endOf('month');
		const date: moment.Moment = startDay.clone().subtract(1, 'day');
		const dayList: Day[] = [];

		while (date.isBefore(endDay, 'day')) {
			dayList.push(
				{
					value: date.add(1, 'day').clone(),
					active: date.isSame(moment(), 'date'),
					disabled: date.isAfter(moment(), 'date'),
					selected: date.isSame(currentDate, 'date')
				}
			);
		}
		this.dayList = dayList.reverse();
	}

	public select(day: moment.Moment): void {
		this.dateService.changeDate(day);
	}
}
