import { Component, OnInit } from '@angular/core';
import { DateService } from 'src/app/services/date.service';
import * as moment from 'moment';

@Component({
	selector: 'app-calendar',
	templateUrl: './calendar.component.html',
	styleUrls: ['./calendar.component.scss']
})
export class CalendarComponent implements OnInit {

	public isDisabled: boolean = false;

	constructor(public dateService: DateService) { }

	public go(direction: number): void {
		this.dateService.changeMonth(direction);
	}

	public ngOnInit(): void {
		this.dateService.date$.subscribe(this.disableForward.bind(this));
	}

	public disableForward(currentDate: moment.Moment): void {
		(currentDate.year() === moment().year())
			&& (currentDate.month() === moment().month())
			? this.isDisabled = true
			: this.isDisabled = false;
	}
}
