import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ExpensesList } from '../models/expenses-list.model';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { GetResponse } from '../models/response.model';
import { MonthExpensesByCategory } from '../models/month-expenses-by-category.model';

@Injectable({
	providedIn: 'root'
})
export class ExpensesService {

	private static url: string = 'https://money-manager-project.firebaseio.com/expenses';

	constructor(private _http: HttpClient) { }

	public loadExpenses(date: moment.Moment): Observable<ExpensesList[]> {
		return this._http
			.get<ExpensesList[]>(`${ExpensesService.url}/${date.format('DD-MM-YYYY')}.json`)
			.pipe(
				map((expenses: any) => {
					if (!expenses) {
						return [];
					}
					return Object.keys(expenses)
						.map((key: string) => ({ ...expenses[key], id: key }));
				})
			);
	}

	public saveExpenses(expenses: ExpensesList): Observable<ExpensesList> {
		return this._http
			.post<GetResponse>(`${ExpensesService.url}/${expenses.day}.json`, expenses)
			.pipe(map((response: any) => {
				return { ...expenses, id: response.name };
			}));
	}

	public removeExpenses(expenses: ExpensesList): Observable<void> {
		return this._http
			.delete<void>(`${ExpensesService.url}/${expenses.day}/${expenses.id}.json`);
	}

}
